</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>modal</td>
<td>是否显示遮罩层</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>closeOnClickModal</td>
<td>是否可以通过点击 modal 层来关闭 popup</td>
<td>boolean</td>
<td></td>
<td>true</td>
</tr>
<tr>
<td>position</td>
<td>popup 的位置</td>
<td>number</td>
<td>bottom、left、top、right、center</td>
<td>bottom</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      visible: false,
